* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Yantramanav", sans-serif;
  background-color: hsl(233, 18%, 91%)
}

.wraper {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  margin: 0 auto;
  width: 95vw;
  min-width: 350px;
  max-width: 1280px;
  height: 100vh;

  h2 {
    font-size: 4.5rem;
    font-weight: 400;
    margin-top: 20px;
    color: hsl(210, 81%, 47%);
    font-family: "Fredoka One", sans-serif;
  }

  @media only screen and (max-width: 700px) {
    h2 {
      font-size: 3.5rem;
    }
  }

  @media only screen and (max-width: 600px) {
    h2 {
      font-size: 2.5rem;
    }
  }

  h3 {
    margin-top: -20px;
    font-size: 1rem;
    font-weight: 300;
    color: hsl(210, 81%, 47%);
  }

  @media only screen and (max-width: 400px) {
    h2 {
      font-size: 2.2rem;
    }
  }
}
